function Authenticate() {
  const _this = this;

  this.login = document.getElementById("login");
  this.pass1 = document.getElementById("pass1");
  this.pass2 = document.getElementById("pass2");
  this.authenBt = document.getElementById("authenticate-bt");

  this.login.onclick = function() {
    _this.login.style.borderColor = "initial";
  };
  this.pass1.onclick = function() {
    _this.pass1.style.borderColor = "initial";
  };
  this.pass2.onclick = function() {
    _this.pass2.style.borderColor = "initial";
  };
  
  this.authenBt.onclick = function() {
    // check inputs 
    if(!_this.login.value) {
      return _this.login.style.borderColor = "red";
    } else if(!_this.pass1.value) {
      return _this.pass1.style.borderColor = "red";
    } else if(!_this.pass2.value) {
      return _this.pass2.style.borderColor = "red";
    }

    let login = _this.login.value;
    let pass1 = _this.pass1.value;
    let pass2 = _this.pass2.value;
    let q = `login=${login}&pass1=${pass1}&pass2=${pass2}`;

    // check passes match
    if(pass1 !== pass2) {
      return alert("Введённые пароли не совпадают");
    }

    request("POST", "/authenticate", q, function(status, res) {
      if(status == "success") {
        alert("Аутентификация успешна, сейчас вы будете перенаправлены на страницу логина");
        return window.location.href = "/auth";
      } else if(status == "error") {
        console.log(res.statusText);
        alert("Ошибка аутентификации, проверьте все поля");
        return;
      }
    });
  };
 } 