function Performance() {

  const _this = this;

  this.pointsData = {
    leftSide: {},
    rightSide: {}
  };
  // 
  this.totalPointsNum = 0;
  this.addPerfBtn = document.getElementById("add-performance-btn");

  this.searchInpt = document.getElementById("search-perfs");
  this.backgroundModal = document.getElementById("background-modal");
  this.closeAddPerf = document.getElementById("close-adding-perf");
  this.savePerfBtn = document.getElementById("save-perf-btn");
  this.perfName = document.getElementById("perf-name");
  this.perfType = document.getElementById("perf-type");
  this.perfUrl = document.getElementById("perf-pic-url");
  this.perfActs = document.getElementById("perf-acts");
  this.perfPoints = document.getElementById("perf-points");
  this.perfNotes = document.getElementById("perf-notes");
  this.addActBtn = document.getElementById("add-act");
  this.searchResults = document.getElementById("search-results");
  this.closeCardPerf = document.getElementById("close-card-perf");
  this.backgroundCard = document.getElementById("card-back");
  this.addPointsBtn = document.getElementById("add-points");
  this.addRightPointBtn = document.getElementById("add-right-pt-btn");
  this.addLeftPointBtn = document.getElementById("add-left-pt-btn");
  this.backgroundPoints = document.getElementById("points-back");
  this.closeEditPointsBtn = document.getElementById("close-points-perf");
  this.userPointsBack = document.getElementById("user-points-back");
  this.closeUserPoints = document.getElementById("close-user-points");

  this.cardPerfName = document.getElementById("card-perf-name");
  this.cardPerfImg = document.getElementById("card-perf-img");
  this.cardPerfType = document.getElementById("card-perf-type");
  this.cardPerfActs = document.getElementById("card-perf-acts");
  this.cardPerfPoints = document.getElementById("card-perf-points");
  this.cardPerfNotes = document.getElementById("card-perf-notes");
  
  this.totalPointsSpan = document.getElementById("total-points-num");

  this.getActsEnding = function(actsNum) {
    let ending;
    if(actsNum == 1) {
      ending = actsNum + " акт";
    } else if(actsNum > 1 && actsNum < 5 ) {
      ending = actsNum + " акта";
    } else {
      ending = actsNum + "актов";
    }
    return ending;
  };

  this.showPerformanceCard = function() {
    // show usual performance card and scroll top
    _this.backgroundCard.style.display = "block";
    window.scrollTo(0, 0);
  };

  this.showUsualPointsTable = function() {
    // show usual performance table and scroll top
    _this.userPointsBack.style.display = "block";
    window.scrollTo(0, 0);
  };

  this.fillCardPerformanceActs = function(type, acts) {
    const cardPerfType = _this.cardPerfType;
    const cardPerfActs = _this.cardPerfActs;

    // fill usual performance card with acts
    if(type === "ballet") {
      cardPerfType.textContent = `Балет, ${_this.getActsEnding(acts.length)}`;
    } 
    else if(type === "opera") {
      cardPerfType.textContent = `Опера, ${_this.getActsEnding(acts.length)}`;
    }

    // clear act's element before filling it
    _this.clearDomElement(cardPerfActs, function() {
      // create and fill element for each act
      for(let e=0; e<acts.length; e++) {
        let actLine = document.createElement("div");
        let span1 = document.createElement("span");
        let span2 = document.createElement("span");

        actLine.setAttribute("class", "act-line");
        span1.textContent = `${e+1} Акт:`;
        span2.textContent = `${acts[e]}`;

        actLine.appendChild(span1);
        actLine.appendChild(span2);
        cardPerfActs.appendChild(actLine);
      }
    });
  };

  this.fillCardPerformancePoints = function(points) {
    const cardPerfPoints = _this.cardPerfPoints;
    
    // if there is no points 
    if(!points) {
      // show that points is absent
      if(cardPerfPoints.childNodes.length == 3) {
        // clear old elements before filling
        cardPerfPoints.removeChild(cardPerfPoints.childNodes[1]);
        cardPerfPoints.removeChild(cardPerfPoints.childNodes[1]);
        // add 'absent' message 
        const i = document.createElement("i");
        i.textContent = "– Отсутствуют";
        cardPerfPoints.appendChild(i);
      }
    }
    else {
      // if there IS points
      if(cardPerfPoints.childNodes.length == 2) {
        // clear user card points div before filling
        cardPerfPoints.removeChild(cardPerfPoints.childNodes[1]);
      } else if(cardPerfPoints.childNodes.length == 3) {
        // clear old elements before filling
        cardPerfPoints.removeChild(cardPerfPoints.childNodes[1]);
        cardPerfPoints.removeChild(cardPerfPoints.childNodes[1]);
      }

      // fill performance card with number of points total
      const totalPoints = document.createElement("p");
      totalPoints.setAttribute("class", "total-points-usr");
      totalPoints.textContent = `Всего: ${_this.countPointsNum(points)} шт.`;
      cardPerfPoints.appendChild(totalPoints);
    
      // fill usual table itself with points
      _this.fillUserPointsTable(points);

      // add show usual points button
      const btn = document.createElement("button");
      btn.setAttribute("class", "showPoints-btn");
      btn.textContent = "Просмотр";
      btn.onclick = function() {
        let userPointsBack = _this.userPointsBack;
        let userPerfCardBack = _this.backgroundCard;

        // set point table's background height as much as performance card background
        userPointsBack.style.height = userPerfCardBack.offsetHeight + "px";

        // show usual table on click
        _this.showUsualPointsTable();
      };
      // append above button to usual card if it is absent
      cardPerfPoints.appendChild(btn);
    }
  };
 
  this.fillPerformanceCard = function(performance) {
    // filling usual performance card
    const cardPerfName = _this.cardPerfName;
    const cardPerfImg = _this.cardPerfImg;
    const cardPerfNotes = _this.cardPerfNotes;
  
    // fill permormance name
    cardPerfName.textContent = performance.name;

    // show performance img if exists
    if(performance.imgUrl) {
      cardPerfImg.src = "/static/imgs/performance-imgs/"+performance.imgUrl;
      cardPerfImg.style.display = "block";
    } else {
      cardPerfImg.src = "";
      cardPerfImg.style.display = "none";
    }
    
    // fill performance acts
    _this.fillCardPerformanceActs(performance.type, performance.acts);
    
    // fill performance card itself with existing points
    _this.fillCardPerformancePoints(performance.points);
   
    // fill notes
    if(performance.notes != "") {
      cardPerfNotes.innerHTML = performance.notes;
    } else {
      cardPerfNotes.textContent = "– Отсутствуют"; 
    }

    // if user is admin, add editing buttons
    if(admin === "true") {
      // add edit performance button
      let editBt = document.createElement("button");
      editBt.setAttribute("class", "edit-btn");
      editBt.textContent = "Редактировать";
      editBt.onclick = function() {
        _this.openEdit(performance);
      };
      // add remove performance button
      let rmBt = document.createElement("button");
      rmBt.setAttribute("class", "rm-perf-btn");
      rmBt.textContent = "Удалить";
      rmBt.onclick = function() {
        if(confirm(`Вы действительно хотите удалить спектакль "${performance.name}" из базы?`)) {
          _this.save(performance._id, "remove");
        }
      };

      // add edit and remove buttons if no ones
      const cardElemCount = cardPerfName.parentElement.childNodes.length;
      if(cardElemCount == 5) {
        cardPerfName.parentElement.appendChild(editBt);
        cardPerfName.parentElement.appendChild(rmBt);
      } 
      else {
        // if there is ones, replace them with new ones
        cardPerfName.parentElement.childNodes[5].remove();
        cardPerfName.parentElement.childNodes[5].remove();
        cardPerfName.parentElement.appendChild(editBt);
        cardPerfName.parentElement.appendChild(rmBt);
      }
    }
    // update function of save performance button of usual card
    _this.savePerfBtn.onclick = function() {
      _this.save(performance._id, "update");
    };
  };

  this.clearDomElement = function(elem, cb) {
    // safe and fast way to clear dom element
    while (elem.firstChild) {
      elem.removeChild(elem.firstChild);
    }
    return cb();
  };

  // filling search results div with found performanes
  this.fillSearchResults = function(results) {

    const srchResults = _this.searchResults;

    // if nothing found
    if(results.length == 0) {
      const el = document.createElement("div");
      el.setAttribute("class", "perf-name-found nothing-found");
      el.textContent = "Ничего не найдено";
      if(!srchResults.childNodes[0]) {
        srchResults.appendChild(el);
      }
    } 
    else {
      // fill search div with elements with performance's names
      for(let i=0; i<results.length; i++) {
        let el = document.createElement("div");
        el.setAttribute("class", "perf-name-found");
        el.textContent = results[i].name;

        // on click -> fill and show perfrormance card modal
        el.onclick = function() {
          _this.fillPerformanceCard(results[i]);
          _this.showPerformanceCard();
        };
        for(let b=0; b<srchResults.childNodes.length; b++) {
          if(srchResults.childNodes[b].textContent == el.textContent) {
            return;
          }
        }
        srchResults.appendChild(el);
      }
    }
  };

  this.searchInpt.oninput = function() {
    if(_this.searchInpt.value && _this.searchInpt.value != "") {
      // clear search results div 
      _this.clearDomElement(_this.searchResults, function() {
        const srchQuery = _this.searchInpt.value;
        const rgx = /^\s*$/;
    
        // regex test if query does not start with empty value
        if(!rgx.test(srchQuery)) {
          const queryStr = `query=${srchQuery}`;
    
          // send the above query to the server
          request("POST", "/perf-search", queryStr, function(status, res) {
            if(status === "success") {
              const results = JSON.parse(res.responseText).perfs;
    
              // fill search div with results found 
              _this.fillSearchResults(results);
            }
            else if(status == "err") {
              return;
            }
          });
        }
      });
    } else {
      setTimeout(function() {
        // clear search results div 
        _this.clearDomElement(_this.searchResults, function(){});
      }, 100);
    }
  };

  this.openEdit = function(performance) {
    _this.pointsData = performance.points;
    
    _this.backgroundCard.style.display = "none";
    _this.backgroundModal.style.display = "block";

    this.perfName.value = performance.name;
    this.perfType.value = performance.type;
    this.perfUrl.value = performance.imgUrl;
    
    // fill editing window with acts
   
    if(performance.acts.length > 0) {
      this.perfActs.innerHTML = "";

      let actLine, sp, ip, rm, add;
      for(let i=0; i<performance.acts.length; i++) {
        actLine = document.createElement("actLine");
        actLine.setAttribute("class", "act-line");

        sp = document.createElement("span");
        sp.setAttribute("class", "perf-act");
        sp.textContent = (i+1)+":";
        actLine.appendChild(sp);
        ip = document.createElement("input");
        ip.setAttribute("class", "act-time");
        ip.setAttribute("type", "text");
        ip.setAttribute("placeholder", "1ч 15мин");
        ip.value = performance.acts[i];
        actLine.appendChild(ip);
        if(i==0) {
          add = document.createElement("button");
          add.setAttribute("class", "add-mini-btn");
          add.textContent = "+";
          add.onclick = function() {
            _this.addAct();
          };
          actLine.appendChild(add);
        } else {
          rm = document.createElement("span");
          rm.setAttribute("class", "del-act-btn");
          rm.textContent = "X";
          rm.onclick = function(e) {
            e.target.parentElement.remove();
          };
          actLine.appendChild(rm);
        }
        em = document.createElement("button");
        sp.setAttribute("class", "perf-act");
        this.perfActs.appendChild(actLine);
      }
      
    }
    _this.fillEditCardPointsDiv(performance.points);
    _this.fillPointsEditModal(performance.points);
    this.perfNotes.value = performance.notes;

    // change text of save button
    this.savePerfBtn.textContent = "Сохранить изменения";
    this.savePerfBtn.onclick = function() {
      _this.save(performance._id, "update");
    };
  };

  this.fillEditCardPointsDiv = function(points) {
    // fill editing table with points
    if(points) {
      // fill preview modal
      _this.perfPoints.childNodes[0].remove();
      _this.perfPoints.childNodes[0].remove();
      let nSp = document.createElement("span");
      nSp.setAttribute("class", "points-number");
      nSp.textContent = _this.countPointsNum(points) + " шт.";
      let openEditTable = document.createElement("span");
      openEditTable.setAttribute("class", "showPoints-btn");
      openEditTable.textContent = "Редактор";
      openEditTable.onclick = function() {
        _this.fillPointsEditModal(points);
        // set edit points' background height as much as edit card background
        _this.backgroundPoints.style.height = _this.backgroundModal.offsetHeight + "px";
        _this.backgroundPoints.style.display = "block";
        window.scrollTo(0, 0);
      }
      _this.perfPoints.appendChild(nSp);
      _this.perfPoints.appendChild(openEditTable);
    } 
  };

  this.isEmpty = function(obj) {
    for(var prop in obj) {
      if(obj.hasOwnProperty(prop)) {
        return false;
      }
    }
    return JSON.stringify(obj) === JSON.stringify({});
  };

  this.fillPointsEditModal = function(points) {
    if(!points) {
      points = {};
    }

    let leftTable = document.getElementById("left-table");
    let rightTable = document.getElementById("right-table");
    
    // clear before filling
    while(leftTable.childNodes.length != 1) {
      leftTable.childNodes[leftTable.childNodes.length-1].remove();
    }
    while(rightTable.childNodes.length != 1) {
      rightTable.childNodes[rightTable.childNodes.length-1].remove();
    }

    _this.totalPointsNum = _this.countPointsNum(points);

    let numTotal = document.getElementById("total-points-num");
    numTotal.textContent = _this.countPointsNum(points) + " шт.";

    let trLeft, trRight, td2, td3, td4;
    for(key1 in points.leftSide) {
      if(_this.isEmpty(points.leftSide[key1])) {
        continue;
      }

      trLeft = document.createElement("tr");
      trLeft.setAttribute("class", "left-point-rope");
      trLeft.appendChild(document.createElement("td"));

      td2 = document.createElement("td");
      td3 = document.createElement("td");
      td4 = document.createElement("td");

      // fill them with inputs
      let inpt1, brSp, inpt2;
      inpt1 = document.createElement("input");
      inpt1.setAttribute("type", "number");
      inpt1.setAttribute("class", "lines");
      inpt1.value = points.leftSide[key1].stalls.split('/')[0];
      brSp = document.createElement("span");
      brSp.textContent = "/";
      inpt2 = document.createElement("input");
      inpt2.setAttribute("type", "number");
      inpt2.setAttribute("class", "lines");
      inpt2.value = points.leftSide[key1].stalls.split('/')[1];
      td2.appendChild(inpt1);
      td2.appendChild(brSp);
      td2.appendChild(inpt2);

      let inptMeters = document.createElement("input");
      inptMeters.setAttribute("type", "number");
      inptMeters.setAttribute("class", "meters");
      inptMeters.value = points.leftSide[key1].meters;
      td3.appendChild(inptMeters);

      let inptMach = document.createElement("input");
      inptMach.setAttribute("type", "number");
      inptMach.setAttribute("class", "machine");
      inptMach.value = points.leftSide[key1].machine;
      td4.appendChild(inptMach);
      let delsp = document.createElement("span");
      delsp.setAttribute("class", "del-point-rope");
      delsp.textContent = "X";
      delsp.onclick = function(e) {
        if(confirm("Удалить эту точку подвеса?")) {
          e.target.parentElement.parentElement.remove();
          _this.totalPointsNum--;
          _this.totalPointsSpan.textContent = _this.totalPointsNum +" шт.";
        }
      };
      td4.appendChild(delsp);

      trLeft.appendChild(td2);
      trLeft.appendChild(td3);
      trLeft.appendChild(td4);
      leftTable.appendChild(trLeft);
    }

    for(key2 in points.rightSide) { 
      if(_this.isEmpty(points.rightSide[key2])) {
        continue;
      }
      trRight = document.createElement("tr");
      trRight.setAttribute("class", "right-point-rope");
      trRight.appendChild(document.createElement("td"));
      td2 = document.createElement("td");
      td3 = document.createElement("td");
      td4 = document.createElement("td");

      // fill them with inputs
      let inpt1, brSp, inpt2;
      inpt1 = document.createElement("input");
      inpt1.setAttribute("type", "number");
      inpt1.setAttribute("class", "lines");
      inpt1.value = points.rightSide[key2].stalls.split('/')[0];
      brSp = document.createElement("span");
      brSp.textContent = "/";
      inpt2 = document.createElement("input");
      inpt2.setAttribute("type", "number");
      inpt2.setAttribute("class", "lines");
      inpt2.value = points.rightSide[key2].stalls.split('/')[1];
      td2.appendChild(inpt1);
      td2.appendChild(brSp);
      td2.appendChild(inpt2);

      let inptMeters = document.createElement("input");
      inptMeters.setAttribute("type", "number");
      inptMeters.setAttribute("class", "meters");
      inptMeters.value = points.rightSide[key2].meters;
      td3.appendChild(inptMeters);

      let inptMach = document.createElement("input");
      inptMach.setAttribute("type", "number");
      inptMach.setAttribute("class", "machine");
      inptMach.value = points.rightSide[key2].machine;
      td4.appendChild(inptMach);
      let delsp = document.createElement("span");
      delsp.setAttribute("class", "del-point-rope");
      delsp.textContent = "X";
      delsp.onclick = function(e) {
        if(confirm("Удалить эту точку подвеса?")) {
          e.target.parentElement.parentElement.remove();
          _this.totalPointsNum--;
          _this.totalPointsSpan.textContent = _this.totalPointsNum +" шт.";
        }
      };
      td4.appendChild(delsp);
      trRight.appendChild(td2);
      trRight.appendChild(td3);
      trRight.appendChild(td4);
      rightTable.appendChild(trRight);
    }
  };

  this.countPointsNum = function(points) {
    let totalNum = 0; 
    if(!points) {
      return 0;
    }
    for(key1 in points.leftSide) {
      if(!_this.isEmpty(points.leftSide[key1])) {
        totalNum++;
      }
    }
    for(key1 in points.rightSide) {
      if(!_this.isEmpty(points.rightSide[key1])) {
        totalNum++;
      }
    }
    return totalNum;
  };

  this.closeUserPoints.onclick = function() {
    // close user table points
    _this.userPointsBack.style.display = "none";
 };

  this.fillUserPointsTable = function(points) {
    let leftUsrTable = document.getElementById("left-usr-table");
    let rightUsrTable = document.getElementById("right-usr-table");
    
    // clear before filling
    while(leftUsrTable.childNodes[1]) {
      leftUsrTable.childNodes[1].remove();
    }
    while(rightUsrTable.childNodes[1]) {
      rightUsrTable.childNodes[1].remove();
    }

    let numTotal = document.getElementById("total-user-points");
    numTotal.textContent = _this.countPointsNum(points) + " шт.";

    let trLeft, trRight, td2, td3, td4;
    for(key1 in points.leftSide) {
      trLeft = document.createElement("tr");
      trLeft.appendChild(document.createElement("td"));

      td2 = document.createElement("td");
      td3 = document.createElement("td");
      td4 = document.createElement("td");

      td2.textContent = points.leftSide[key1].stalls; 
      trLeft.appendChild(td2);
      td3.textContent = points.leftSide[key1].meters;
      trLeft.appendChild(td3);
      td4.textContent = points.leftSide[key1].machine;
      trLeft.appendChild(td4);
      leftUsrTable.appendChild(trLeft);
    }

    for(key2 in points.rightSide) { 
      trRight = document.createElement("tr");
      trRight.appendChild(document.createElement("td"));
      td2 = document.createElement("td");
      td3 = document.createElement("td");
      td4 = document.createElement("td");
      
      td2.textContent = points.rightSide[key2].stalls;
      trRight.appendChild(td2);
      td3.textContent = points.rightSide[key2].meters;
      trRight.appendChild(td3);
      td4.textContent = points.rightSide[key2].machine;
      trRight.appendChild(td4);
      rightUsrTable.appendChild(trRight);
    }
  };

  this.saveEditingPoints = function() {
    // save points
    let leftPoints = document.getElementsByClassName("left-point-rope");
    let rightPoints = document.getElementsByClassName("right-point-rope");
  
    // clear points data before filling it
    _this.pointsData = {
      leftSide: {},
      rightSide: {}
    };

    if(leftPoints.length == 0 && rightPoints.length == 0) {
      _this.backgroundPoints.style.display = "none";
      return;
    }

    for(let i=0; i<leftPoints.length; i++) {
      _this.pointsData.leftSide["point"+(i+1)] = {};
      for(let b=1; b<leftPoints[i].childNodes.length; b++) {
        if(b==1) {
          let line1 = leftPoints[i].childNodes[b].firstChild;
          let line2 = leftPoints[i].childNodes[b].lastChild;
          // check if empty
          if(!line1.value) {
            line1.parentElement.style.backgroundColor = "red";
            line1.onclick = function() {
              line1.parentElement.style.backgroundColor = "initial";
            };
            alert("Пустые поля недопустимы, заполните либо удалите пустые точки подвеса");
            return;
          } else if(!line2.value) {
            line2.parentElement.style.backgroundColor = "red";
            line2.onclick = function() {
              line2.parentElement.style.backgroundColor = "initial";
            };
            alert("Пустые поля недопустимы, заполните либо удалите пустые точки подвеса");
            return;
          }
          _this.pointsData.leftSide["point"+(i+1)].stalls = line1.value+"/"+line2.value;
        } else if (b==2) {
          // check if empty
          if(!leftPoints[i].childNodes[b].firstChild.value) {
            leftPoints[i].childNodes[b].firstChild.parentElement.style.backgroundColor = "red";
            leftPoints[i].childNodes[b].firstChild.onclick = function() {
              leftPoints[i].childNodes[b].firstChild.parentElement.style.backgroundColor = "initial";
            }
            alert("Пустые поля недопустимы, заполните либо удалите пустые точки подвеса");
            return;
          }
          _this.pointsData.leftSide["point"+(i+1)].meters = leftPoints[i].childNodes[b].firstChild.value;
        } else if (b==3) {
          _this.pointsData.leftSide["point"+(i+1)].machine = leftPoints[i].childNodes[b].firstChild.value;
        }
      }
    }
    for(let i=0; i<rightPoints.length; i++) {
      _this.pointsData.rightSide["point"+(i+1)] = {};
      for(let b=1; b<rightPoints[i].childNodes.length; b++) {
        if(b==1) {
          let line1 = rightPoints[i].childNodes[b].firstChild;
          let line2 = rightPoints[i].childNodes[b].lastChild;

          // check if empty
          if(!line1.value) {
            line1.parentElement.style.backgroundColor = "red";
            line1.onclick = function() {
              line1.parentElement.style.backgroundColor = "initial";
            };
            alert("Пустые поля недопустимы, заполните либо удалите пустые точки подвеса");
            return;
          } else if(!line2.value) {
            line2.parentElement.style.backgroundColor = "red";
            line2.onclick = function() {
              line2.parentElement.style.backgroundColor = "initial";
            };
            alert("Пустые поля недопустимы, заполните либо удалите пустые точки подвеса");
            return;
          }

          _this.pointsData.rightSide["point"+(i+1)].stalls = line1.value+"/"+line2.value;
        } else if (b==2) {
           // check if empty
          if(!rightPoints[i].childNodes[b].firstChild.value) {
            rightPoints[i].childNodes[b].firstChild.parentElement.style.backgroundColor = "red";
            rightPoints[i].childNodes[b].firstChild.onclick = function() {
              rightPoints[i].childNodes[b].firstChild.parentElement.style.backgroundColor = "initial";
            }
            alert("Пустые поля недопустимы, заполните либо удалите пустые точки подвеса");
            return;
          }
          _this.pointsData.rightSide["point"+(i+1)].meters = rightPoints[i].childNodes[b].firstChild.value;
        } else if (b==3) {
          _this.pointsData.rightSide["point"+(i+1)].machine = rightPoints[i].childNodes[b].firstChild.value;
        }
      }
    }

    // fill editing card points div
    _this.fillEditCardPointsDiv(_this.pointsData);
    _this.backgroundPoints.style.display = "none";
  };

  this.closeEditPointsBtn.onclick = function() {
    _this.saveEditingPoints();
  };

  this.addPointsBtn.onclick = function() {
    _this.backgroundPoints.style.display = "block";
    window.scrollTo(0, 0);
  };

  this.closeCardPerf.onclick = function() {
    _this.savePerfBtn.onclick = function() {
      _this.save(false, "create");
    };

    _this.backgroundCard.style.display = "none";
  };

  if(admin === "true") {
    this.addPerfBtn.onclick = function() {
      _this.backgroundModal.style.display = "block";
    };
  }

  this.closeAddPerf.onclick = function() {
    _this.backgroundModal.style.display = "none";
  };

  this.savePerfBtn.onclick = function() {
    _this.save(false, "create");
  };

  this.perfName.onclick = function() {
    _this.perfName.style.borderColor = "initial";
  };

  this.perfType.onclick = function() {
    _this.perfType.style.borderColor = "initial";
  }

  this.addActBtn.onclick = function() {
    _this.addAct();
  };

  this.addRightPointBtn.onclick = function() {
    _this.addPoint("right");
  }

  this.addLeftPointBtn.onclick = function() {
    _this.addPoint("left");
  }

  this.addAct = function() {
    // num of childnodes without button
    let childNum = _this.perfActs.childNodes.length;
    let nextChildNum = childNum+1;
   
    // create elements
    let actLine = document.createElement("div");
    let spanAct = document.createElement("span");
    let timeInpt = document.createElement("input");
    let delBtn = document.createElement("span");

    
    // set attributes on them
    actLine.setAttribute("class", "act-line")
    spanAct.setAttribute("class", "perf-act");
    spanAct.setAttribute("id", "perf-act"+nextChildNum);
    spanAct.textContent = nextChildNum +":";
    timeInpt.setAttribute("type", "text");
    timeInpt.setAttribute("class", "act-time");
    timeInpt.setAttribute("id", "act-time"+nextChildNum);
    timeInpt.setAttribute("placeholder", "1ч 15мин");
    delBtn.setAttribute("id", "del-act-btn"+nextChildNum);
    delBtn.setAttribute("class", "del-act-btn");
    delBtn.textContent = "X";
    delBtn.onclick = function() {
      delBtn.parentElement.remove();
    };

    // add
    actLine.appendChild(spanAct);
    actLine.appendChild(timeInpt);
    actLine.appendChild(delBtn);
    _this.perfActs.insertBefore(actLine, _this.perfActs.childNodes[childNum]);
  };

  this.checkActs = function() {
    // num of childnodes
    let childNum = _this.perfActs.childNodes.length;
    let children = _this.perfActs.childNodes;
    for(let i=0; i<childNum; i++) {
      
      // check if act time exists for all children
      if(!children[i].childNodes[1].value) {
        children[i].childNodes[1].style.borderColor = "red";
        children[i].childNodes[1].onclick = function() {return children[i].childNodes[1].style.borderColor = "initial";};
        return false;
        break;
      }
    }
    return true;
  };

  this.inputsValid = function() {
    // check all inputs
    if(!_this.perfName.value) {
      _this.perfName.style.borderColor = "red";
      return false;
    } 
    else if (!_this.perfType.value) {
      _this.perfType.style.borderColor = "red";
      return false;
    } 
    else if(!_this.checkActs()) {
      _this.perfActs.borderColor = "red";
      return false;
    } 
    return true;
  };

  this.save = function(perfId, action) {
    if(action === "update" || action === "create") {
      // check for empty inputs
      if(_this.inputsValid()) {
        let name = _this.perfName.value;
        let type = _this.perfType.value;
        let imgUrl = _this.perfUrl.value;
        let perfActs = [];
        let perfNotes = _this.perfNotes.value;
        
        // check if acts do not include commas
        for(let i=0; i<_this.perfActs.childNodes.length; i++) {
          if(_this.perfActs.childNodes[i].childNodes[1].value.includes(",")) {
            _this.perfActs.childNodes[i].childNodes[1].style.borderColor = "red";
            return;
          }
          // add act to perfActs to save if validation passed
          perfActs.push(_this.perfActs.childNodes[i].childNodes[1].value);
        }
  
        if(perfId && action === "update") {
          let perfData = `name=${name}&type=${type}&imgUrl=${imgUrl}&acts=${perfActs}&points=${JSON.stringify(_this.pointsData)}&notes=${perfNotes}&perfId=${perfId}&action=${action}`;
  
          // send data to Server
          request("post", "/performances", perfData, function(status, res) {
            if(status == "success") {
              alert("Спектакль обновлен успешно");
              _this.backgroundModal.style.display = "none";
              window.location.reload();
            }
            else if(status == "err") {
              return;
            }
          });
        } else if(!perfId && action== "create") { 
          let perfData = `name=${name}&type=${type}&imgUrl=${imgUrl}&acts=${perfActs}&points=${JSON.stringify(_this.pointsData)}&notes=${perfNotes}&action=${action}`;
          // send data to Server
          request("post", "/performances", perfData, function(status, res) {
            if(status == "success") {
              alert("Новый спектакль успешно добавлен");
              _this.backgroundModal.style.display = "none";
              window.location.reload();
            }
            else if(status == "err") {
              return;
            }
          });
        }
      }
    } 
    else if(action === "remove") {
      perfData = `perfId=${perfId}&action=${action}`;
      // send data to Server
      request("post", "/performances", perfData, function(status, res) {
        if(status == "success") {
          alert("Спектакль успешно удалён");
          _this.backgroundModal.style.display = "none";
          window.location.reload();
        }
        else if(status == "err") {
          return;
        }
      });
    }
  };

  this.addPoint = function(side) {
    let tr = document.createElement("tr");
    let brSpan = document.createElement("span");
    brSpan.textContent = "/";

    for(let i=0; i<4; i++) {
      let td = document.createElement("td");
      if(i == 1) {
        for(let b=0; b<2; b++) {
          let inpt = document.createElement("input");
          inpt.setAttribute("type", "number");
          inpt.setAttribute("class", "lines");
          (b == 1 ? td.appendChild(brSpan):null);
          td.appendChild(inpt);
        }
      } else if(i == 2) {
        let inpt = document.createElement("input");
        inpt.setAttribute("type", "number");
        inpt.setAttribute("class", "meters");
        td.appendChild(inpt);
      } else if(i == 3) {
        let inpt = document.createElement("input");
        let delSp = document.createElement("span");
        delSp.textContent = "X";
        delSp.setAttribute("class", "del-point-rope");
        delSp.onclick = function(e) {
          if(confirm("Удалить эту точку подвеса?")) {
            e.target.parentElement.parentElement.remove();
            _this.totalPointsNum--;
            _this.totalPointsSpan.textContent = _this.totalPointsNum +" шт.";
          }
        };
        inpt.setAttribute("type", "number");
        inpt.setAttribute("class", "machine");
        td.appendChild(inpt);
        td.appendChild(delSp);
      }
      tr.appendChild(td);
    }
    // add
    tr.setAttribute("class", side+"-point-rope");
    document.getElementById(side+"-table").appendChild(tr);

    // increment total points
    _this.totalPointsNum++;
    _this.totalPointsSpan.textContent = _this.totalPointsNum + " шт.";
  }; 
}