function Auth() {
  const _this = this;
  this.authBt = document.getElementById("auth-bt");
  this.login = document.getElementById("login");
  this.pass = document.getElementById("pass");

  this.authBt.onclick = function() {
    let login = _this.login.value;
    let pass = _this.pass.value;

    if(!login || !pass) {
      return alert("Отсутствует логин или пароль");
    }
    request("POST", "/auth", `login=${login}&pass=${pass}`, function(status, res) {
      if(status == "success") {
        alert("Авторизация успешна и будет действительна в течение 30 дней");
        return window.location.href = "/";
      } else if(status == "error") {
        alert("Ошибка авторизации, проверьте введённые Вами данные");
        return;
      }
    });
  }
}